.flip-image {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  padding-right: 100px;
}
.flip-image div {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

.main-body {
  width: 100%;
  height: 650px;
  position: relative;
  background: url(/react-portfolio/static/media/hero3.f1dfecc6.jpg) no-repeat center center fixed;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 75px;
  padding-left: 100px;
}

.main-greeting {
  font-size: 100px;
  color: white;
}

.hero-copyright {
  font-size: 10px;
  color: #fff6;
  position: absolute;
  bottom: 6px;
  left: 8px;
}

@media (max-width: 750px) {
  .flip-image {
    padding-right: 20px;
  }
  .main-body {
    width: 100%;
    padding-left: 20px;
    padding-bottom: 40px;
    background-position: 55% center;
  }

  .main-greeting {
    font-size: 48px;
  }
}

