.projects-body {
    width: 100%;
    min-height: 500px;
    background:-webkit-gradient(linear, left bottom, right top, from(#73788E), to(#B7BBAE) );
    background:-webkit-linear-gradient(bottom left, #73788E, #B7BBAE );
    background:linear-gradient(to top right, #73788E, #B7BBAE );
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 80px 100px 160px 100px;
    position: relative;
}

.projects-title {
    font-size: 60px;
    color: #fff;
    margin-bottom: 80px;
}

.projects-display {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.projects-hint-banner {
    width: 275px;
    height: 40px;
    color: white;
    background: rgba( 0, 0, 0, 0.6 );
    font-size: 12px;
    position: absolute;
    left: 0;
    top: 34px;
    box-shadow: -4px 4px 10px rgba( 0, 0, 0, 0.4 );
    border-radius: 0 5px 5px 0;
    padding: 6px 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.projects-hint-banner p {
    margin: 0px;
}

.projects-project {
    height: 490px;
    width: 375px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    background: rgba( 0, 0, 0, 0.4 );
    border-radius: 5px;
    font-size: 14px;
    margin-bottom: 60px;
    margin-right: 30px;
    margin-left: 30px;
}

.project-gh-link {
    width: 17px !important;
}

.projects-project-title {
    width: 100%;
    height: 46px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    padding: 10px;
}

.title-title {
    display: flex;
    align-items: center;
}

.projects-project-info {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.projects-project-details {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    padding-bottom: 4px;
}

.project-details-box {

}

.project-details-box::-webkit-scrollbar {
    width: 8px;
    margin-right: 4px;
}

.project-details-box::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba( 0, 0, 0, 0.4 );
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.projects-tech {
    font-weight: 600;
    margin-bottom: 8px;
    font-size: 15px;
}

.projects-project img {
    width: 375px;
    max-height: 196px;
    background-size: cover;
}

.projects-project p {
    padding: 0 20px;
}

@media (max-width: 750px) {
    .projects-body {
        padding: 0;
    }

    .projects-title {
        font-size: 40px;
        color: #fff;
        margin: 80px 0 0 0;
    }

    .projects-display {
        flex-direction: column;
        align-items: center;
        margin-top: 40px;
    }

    .projects-project {
        width: 300px;
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;

    }

    .projects-project img {
        width: 300px;
    }
}
