html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

.contact-body {
  width: 50%;
  height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgb(85, 85, 85);
}

.contact-header {
  font-size: 40px;
  color: white;
  margin-top: 40px;
}

.contact-links {
  height: 75px;
  width: 250px;
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.contact-links img {
  width: 80px;
}

@media (max-width: 750px) {
  .contact-body {
    width: 100%;
    height: 250px;
  }

  .contact-header {
    margin-top: 20px;
  }
}
